import React, { Component } from 'react'
import css from 'containers/Header/Header.module.scss'
import { Link, withRouter } from "react-router-dom"
import Container from "components/Grid/Container"
import classNames from 'classnames'
import axios from 'axios-instance'
import Logo from 'components/Logo/Logo'

class Header extends Component {
  constructor(props) {
    super(props)
    this.headerRef = React.createRef()
  }

  state = {
    headerData: null,
    isMenuVisible: false,
    scrollSize: null,
    isHeaderFixedVisible: false,
    isHeaderFixedHidden: false,
    isHeaderStatic: true
  }

  componentDidMount() {
    axios.get('/header.json')
      .then(response => {
        this.setState(prevState => {
          return {
            ...prevState,
            headerData: response.data
          }
        })
        this.props.getSize(this.headerRef.current.getBoundingClientRect())
      })
      .catch(error => console.log('Error getting document', error))

    this.setState(prevState => {
      return {
        ...prevState,
        scrollSize: window.scrollY
      }
    })
  }

  handleClickBurger = () => {
    this.setState(prevState => {
      return {
        ...prevState,
        isMenuVisible: !prevState.isMenuVisible
      }
    })
  }


  icon = () => (
    <svg className={css.icon} viewBox='0 0 35 25' stroke="none" xmlns='http://www.w3.org/2000/svg'>
      <rect x="0" y="0" width="35" height="2" />
      {!this.state.isMenuVisible &&
        <g className={css.iconMiddle}>
          <rect x="0" y="12" width="17" height="2" />
        </g>
      }
      {this.state.isMenuVisible &&
        <g className={css.iconClose}>
          <rect x="12" y="12" width="12" height="2" />
          <rect x="17" y="7" width="2" height="12" />
        </g>
      }
      <g className={css.iconBottom} style={{ transform: this.state.isMenuVisible && 'translateX(0.3rem)' }}>
        <rect x="0" y="23" width="29" height="2" />
      </g>
    </svg>
  )

  render () {
    const {
      headerData,
      isMenuVisible,
    } = this.state

    const navigation = headerData && headerData.map((item, index) => {
      return (
        <li className={css.item} key={index}>
          <Link to={item.url} className={css.link}>
            {item.text}
          </Link>
        </li>
      )
    })

    return (
      <header className={css.header}>
        <Container className={css.wrapper}>
          <Logo className={css.logo} />
          <div className={css.navWrapper}>
            <nav className={classNames(css.nav, { [css.navOpened]: isMenuVisible })}>
              <ul className={css.list}>
                {navigation}
              </ul>
            </nav>
          </div>
          <button className={css.burger} type='button' onClick={this.handleClickBurger}>
            Управление адаптивным меню
            {this.icon()}
          </button>
        </Container>
      </header>
    )
  }
}

export default withRouter(Header);
